.header {
  position: absolute;
  content: "";
  width: 100%;

  @include vp-767 {
    &:has(.header__nav--opened) {
      .header__logo {
        display: none;
      }
    }
  }
}

.header__container {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
  z-index: 3;

  @include vp-767 {
    padding-top: 19px;
  }
}

.header__logo {
  &:hover,
  &:focus-visible {
    opacity: 0.7;
    outline: none;
  }

  &:active {
    opacity: 0.4;
  }
}

.header__nav {
  transition: 0.3s all ease-in-out;
}

.header__nav--opened {
  transition: all ease 0.5s;

  @include vp-767 {
    min-width: 290px;
    width: 95%;
    margin: 0 auto;
  }
}

.header__nav__btn {
  transition: all ease 0.5s;
}

.header__nav__btn span:nth-child(1n) {
  width: 44px;
  max-width: 44px;
}

